
nav ul {
  list-style: none;
  padding: 0;
  display: flex;
  justify-content: center;
  margin-bottom: 0;
}

nav ul li {
  margin: 0 0.5rem;
}

.navitems {
  color: black;
}

a {
  color: black;
}
.signWrapper {
    margin: auto;
    width: 50%;
    padding: 15px;
}

.signHeader {
    margin: auto;
    width: 50%;
    padding: 30px;
}
.hero {
  height: 500px;
  background-size: cover;
  background-position: center;
  position: relative;
}

.hero::after {
  content: "";
  display: block;
  opacity: .4;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: black;
}

.hero * {
  position: relative;
  z-index: 2;
}

.hero h1, .hero h2 {
  color: white;
}

.hero h1 {
  font-size: 60px;
  padding-top: 140px;
  margin-top: 0;
}

* {
  box-sizing: border-box;
}

html,
body,
#root,
#root > div,
div[data-reactroot] {
  height: 100%;
  padding: 0;
  margin: 0;
}

.formWrapper {
  padding: 30px 100px 20px 100px;
}

.formHeader {
  padding: 20px 0px 20px 0px;
}
